.container{--grid-gutter-x: 0 !important;padding:0 !important}.hero-summary{position:relative}.hero-summary__banner{width:100%;height:auto;display:block}.hero-summary__desc{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%)}.hero-summary__intro{font-size:.9rem;border-radius:30px;padding:5px 0;margin:0 50px;color:#1d3e1d;background-color:#b6e0b6}@media(max-width: 575.98px){.hero-summary__intro{font-size:.7rem;padding:3px 0;margin:0 35px}}.hero-summary__heading{margin:14px 0;font-size:2.8rem;font-weight:600;color:#fff}@media(max-width: 575.98px){.hero-summary__heading{font-size:1.8rem}}.hero-summary__text{font-size:1.2rem;color:#c8bbbb;margin-bottom:20px}@media(max-width: 575.98px){.hero-summary__text{font-size:.9rem}}.hero-summary__list{list-style:none;padding:0;margin-bottom:20px}@media(max-width: 575.98px){.hero-summary__list{display:none}}.hero-summary__list-item{font-size:1rem;color:#f0e3e3;padding:1px 0}.hero-summary__list-item::before{content:"✓";padding-right:4px;display:inline-block}.action-btn{padding:4px 30px;font-size:1rem;color:#fff;border:2px solid #fff;background-color:rgba(0,0,0,0);cursor:pointer}.step{background-color:#f5f5ef}.step__number{font-size:3rem;font-weight:700;color:#8eb17d;margin-bottom:10px;text-align:left;margin:20px 0 0 20px}.step__heading{font-size:1.8rem;font-weight:600;margin:50px 0 20px 20px;text-align:left;margin-left:20px}.step__desc{font-size:1rem;color:#555151;margin:0 20px 20px 20px;text-align:left}.step img{height:400px;display:block;object-fit:cover}.contact{background-color:#f5f5ef}.contact__heading{font-size:2.5rem;font-weight:600;margin:20px 0 20px 20px;text-align:left;margin-left:20px}.contact__email,.contact__phone{font-size:1rem;color:#8eb17d;margin:0 20px 10px 20px;padding:10px 0;text-align:left}.contact__info{font-size:1rem;color:#555151;margin:0 20px 10px 20px;padding:10px 0;text-align:left}